.card {
  background: #fff;
  border-radius: 15px;
  height: 200px;
  margin: 0.75rem;
  position: relative;
  /* width: 325px; */
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:rgb(220, 194, 108);
  border-radius: 15px;
}
.card > .img-container > img {
  width: 90%;
}
