.navbar-background {
  background-color: white;
  border-radius: 15px;
  padding: 3px;
}

.navbar {
  width: 100%;
  background-color: rgb(131, 204, 71);
  border-radius: 15px;
}
.wrapper {
  padding-top: 50px;
  background-image: url("http://s-tlk.org/news/tlg_background1.jpg");
  background-size: cover;
  background-position: center;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

